'use client';
import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import "./globals.css";
import * as React from 'react';
import ThemeRegistry from '../components/ThemeRegistry';
import { useEffect, useState } from 'react';
import { usePathname, useRouter } from 'next/navigation';

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

export default function RootLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  const router = useRouter();
  const [authTick, setAuthTick] = useState(0);

  useEffect(() => {
    const onStorage = () => setAuthTick(tick => tick + 1);
    window.addEventListener('storage', onStorage);
    return () => window.removeEventListener('storage', onStorage);
  }, []);

  useEffect(() => {
    const isLoggedIn = typeof window !== 'undefined' && localStorage.getItem('isLoggedIn') === 'true';
    console.log('Layout check:', { isLoggedIn, pathname });
    // If logged in, block access to login/register
    if (isLoggedIn && (pathname.startsWith('/login') || pathname.startsWith('/register'))) {
      router.replace('/dashboard');
    }
    // If not logged in, block access to dashboard/features
    if (!isLoggedIn && (
      pathname.startsWith('/dashboard') ||
      pathname.startsWith('/compose') ||
      pathname.startsWith('/inbox') ||
      pathname.startsWith('/keys') ||
      pathname.startsWith('/test')
    )) {
      router.replace('/login');
    }
  }, [pathname, router, authTick]);

  return (
    <html lang="en">
      <body>
        <ThemeRegistry>
          {children}
        </ThemeRegistry>
      </body>
    </html>
  );
}
